import React from "react";

class MintHeader extends React.Component {
  render() {
    return (
      <>
        <section className="container nftnav-container" style={{ background: `url('img/background/frame_6.png')` }}>
          <div className="content mx-auto">
            <div className="row m-t-60 mb-6 msg-content">
              <div className="col-md-12">
                <div className='text-center'>
                  <h2>Purchase NFT</h2>
                </div>
              </div>
              <div className="col-md-4 offset-md-2 col-sm-12">
                <div className="msg-content-item">
                  <div className="item-icon">
                    <img src="img/background/icon_1.png" alt=""></img>
                  </div>
                  <div className="fs-20 c-app">Weekly NFT</div>
                  <span className="fs-18 c-more">1,000 AVAX</span>
                  <button className="btn-main lead round-button">BUY NFT</button>
                </div>
              </div>
              <div className="col-md-4 col-sm-12">
                <div className="msg-content-item">
                  <div className="item-icon">
                    <img src="img/background/icon_2.png" alt=""></img>
                  </div>
                  <div className="fs-20 c-app">Bi-Daily NFT</div>
                  <span className="fs-18 c-more">1,000 AVAX</span>
                  <button className="btn-main lead round-button">BUY NFT</button>
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
};

export default MintHeader;