import React from "react";

const defaultData = [
  {
    no: 1,
    date: '2022/01/05',
    amount: 10
  },
  {
    no: 2,
    date: '2022/01/05',
    amount: 10
  },
  {
    no: 3,
    date: '2022/01/05',
    amount: 10
  },
  {
    no: 4,
    date: '2022/01/05',
    amount: 10
  },
  {
    no: 5,
    date: '2022/01/05',
    amount: 100
  },
  {
    no: 6,
    date: '2022/01/05',
    amount: 1000
  },
  {
    no: 7,
    date: '2022/01/05',
    amount: 1000
  },
  {
    no: 8,
    date: '2022/01/05',
    amount: 10000
  },
  {
    no: 9,
    date: '2022/01/05',
    amount: 1000
  },
];

class NftHistory extends React.Component {
  render() {
    return (
      <>
        <section className="container">
          <div className="content mx-auto">
            <div className="row m-t-60 mb-6 msg-content">
              <div className="offset-md-1 col-md-5 col-sm-12">
                <div className="history-content-title">
                  <button className="btn-page left">
                    <i class="fa fa-arrow-left" aria-hidden="true"></i>
                  </button>
                  <span className="fs-20 c-app text-white">Weekly Lottery</span>
                  <button className="btn-page right">
                    <i class="fa fa-arrow-right" aria-hidden="true"></i>
                  </button>
                </div>
                <div className="history-content-item">
                  {defaultData.map((item, index) => (
                    <div className="history-item" key={index} style={{ backgroundColor: index % 2 === 0 ? 'white' : '#e5f5f1' }}>
                      <div className="row">
                        <div className="col-md-3 col-sm-3 col-xs-3">
                          <span>{item.no}.</span>
                        </div>
                        <div className="col-md-4 col-sm-4 col-xs-4">
                          <span>{item.date}</span>
                        </div>
                        <div className="col-md-5 col-sm-5 col-xs-5" align="right">
                          <span>{item.amount} AVAX</span>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
              <div className="col-md-5 col-sm-12">
                <div className="history-content-title">
                  <button className="btn-page left">
                    <i class="fa fa-arrow-left" aria-hidden="true"></i>
                  </button>
                  <span className="fs-20 c-app text-white">Bi-Daily Lottery</span>
                  <button className="btn-page right">
                    <i class="fa fa-arrow-right" aria-hidden="true"></i>
                  </button>
                </div>
                <div className="history-content-item">
                  {defaultData.map((item, index) => (
                    <div className="history-item" key={index} style={{ backgroundColor: index % 2 === 0 ? 'white' : '#e5f5f1' }}>
                      <div className="row">
                        <div className="col-md-3 col-sm-3 col-xs-3">
                          <span>{item.no}.</span>
                        </div>
                        <div className="col-md-4 col-sm-4 col-xs-4">
                          <span>{item.date}</span>
                        </div>
                        <div className="col-md-5 col-sm-5 col-xs-5" align="right">
                          <span>{item.amount} AVAX</span>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
};

export default NftHistory;